<template>
  <div class="home">
    <image-container :image-url="backgroundImage" should-show-as-background />
    <marquee
      :direction="marqueeTextDirection"
      :scrolldelay="marqueeTextScrolldelay"
      truespeed
      class="marquee-text"
    >
      {{ randomQuote }}
    </marquee>
    <router-link to="/weatherpage" class="routing-tag">
      <button-element text="Checkout the weather" />
    </router-link>
  </div>
</template>

<script setup lang="ts">
import axios from "axios";
import { onMounted, ref } from "vue";

import { RandomQuote } from "../types/Enums";

import backgroundImage from "../assets/homepage.jpg";
import ButtonElement from "../components/ButtonElement.vue";
import ImageContainer from "../components/ImageContainer.vue";

const randomQuote = ref<string>("");

const marqueeTextDirection = "left";
const marqueeTextScrolldelay = 30;

const getRandomQuote = async () => {
  await axios.get(RandomQuote.RANDOM_QUOTE_URL).then((result) => {
    randomQuote.value = result.data.quote;
  });
};

setInterval(getRandomQuote, RandomQuote.RANDOM_QUOTE_TIME_INTERVAL);

onMounted(getRandomQuote);
</script>

